/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { Paper, Grid, Hidden, Typography } from '@material-ui/core';
import { useSelector } from 'react-redux';
import clsx from 'clsx';
import { toast } from 'react-toastify';

import Loader from '../../../components/Loader';
import useStyles from './styles';

import api from '../../../services/api';

import Task from './Task';
import FreeTask from './FreeTask';

export default function Tasks() {
  const classes = useStyles();
  const myTeam = useSelector((state) => state.team.team);
  const loading = useSelector((state) => state.team.loading);
  const [tasks, setTasks] = useState([]);
  const [loadingTasks, setLoadingTasks] = useState(false);

  async function getTasks() {
    setLoadingTasks(true);

    try {
      let response = null;

      if (myTeam) {
        response = await api.get(`tasks/${myTeam.teamId}?type=${myTeam.type}`);
      } else {
        response = await api.get(`tasks/noTeam?type=Geral`);
      }

      setTasks(response.data);

      setLoadingTasks(false);
    } catch (error) {
      setLoadingTasks(false);
      toast.error('Ocorreu algum erro ,tente novamente mais tarde.');
    }
  }

  useEffect(() => {
    if (!loading) {
      getTasks();
    }
  }, [loading]);

  function freeTasks() {
    const array = [];

    for (let i = 24; i > tasks.length; i--) {
      array.push(null);
    }

    return array;
  }

  return (
    <Paper
      className={clsx(
        classes.paper,
        (loading || loadingTasks) && classes.loading
      )}
    >
      {loading || loadingTasks ? (
        <Loader size={50} />
      ) : (
        <Grid container spacing={2}>
          <Hidden md mdUp>
            <Grid item xs={12}>
              <Typography className={classes.title}>Desafíos</Typography>
            </Grid>
          </Hidden>

          {tasks.map((task) => (
            <Task data={task} att={() => getTasks()} key={task.task._id} />
          ))}

          {freeTasks().map((task, index) => (
            <FreeTask
              number={index + tasks.length + 1}
              key={`freeTask${index}`}
            />
          ))}
        </Grid>
      )}
    </Paper>
  );
}
