import React, { useState, useRef } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  Container,
  Paper,
  Button,
  Typography,
  Grid,
  Hidden,
  CircularProgress,
} from '@material-ui/core';
import clsx from 'clsx';

import { Form } from '@unform/web';
import * as Yup from 'yup';
import Input from '../../../components/Input';
import logo from '../../../assets/logo.svg';
import useStyles from '../SignIn/styles';
import useStyles2 from './styles';
import dateMask from '../../../components/maskInput/date';

import BrValidator from './validators/br';
import OutValidator from './validators/out';

import { signUpRequest } from '../../../store/modules/auth/actions';

export default function SignUp() {
  const dispatch = useDispatch();
  const [type, setType] = useState(1);
  const classes = useStyles();
  const classes2 = useStyles2();
  const formRef = useRef();
  const loading = useSelector((state) => state.auth.loading);

  const [register, setRegister] = useState('');
  const [dataOut, setDataOut] = useState({
    name: '',
    email: '',
    date: '',
  });

  async function handleSubmit() {
    formRef.current.setErrors({});

    try {
      if (type === 1) {
        await BrValidator({ register });

        dispatch(signUpRequest({ register }));
      } else {
        await OutValidator(dataOut);

        dispatch(signUpRequest(dataOut));
      }
    } catch (err) {
      const validationErrors = {};
      if (err instanceof Yup.ValidationError) {
        err.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });

        formRef.current.setErrors(validationErrors);
      }
    }
  }

  function handleChange(e, name) {
    setDataOut({
      ...dataOut,
      [name]: e.target.value,
    });
  }

  return (
    <Container maxWidth="sm">
      <Paper className={classes.paper}>
        <Grid Container spacing={2}>
          <Grid item xs={12}>
            <Grid item xs={12}>
              <img className={classes.logo} src={logo} />
            </Grid>

            <Grid item xs={12} className={classes.descriptionDiv}>
              <Typography className={classes.description}>
                Bem-vindo(a) ao Joti Challenge 2020!
              </Typography>
              <Hidden smDown>
                <Typography className={classes.description}>
                  Preencha os campos para se cadastrar na plataforma
                </Typography>
              </Hidden>
            </Grid>

            <Grid item xs={12} justify="center">
              <Grid container>
                <Grid item xs={6}>
                  <Button
                    fullWidth
                    onClick={() => setType(1)}
                    style={{ borderRadius: '3px 0px 0px 3px' }}
                    className={clsx(
                      classes2.button,
                      type === 1 && classes2.selected
                    )}
                  >
                    Brasileiro(a)
                  </Button>
                </Grid>

                <Grid item xs={6}>
                  <Button
                    fullWidth
                    onClick={() => setType(2)}
                    style={{ borderRadius: '0px 3px 3px 0px' }}
                    className={clsx(
                      classes2.button,
                      type === 2 && classes2.selected
                    )}
                  >
                    Externo(a)
                  </Button>
                </Grid>
              </Grid>
            </Grid>

            <Grid item xs={12}>
              <Form
                ref={formRef}
                onSubmit={handleSubmit}
                className={classes.form}
              >
                {type === 1 ? (
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <Input
                        name="register"
                        label="Registro"
                        value={register}
                        onChange={(e) => setRegister(e.target.value)}
                      />
                    </Grid>
                  </Grid>
                ) : (
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <Input
                        name="name"
                        label="nome"
                        value={dataOut.name}
                        onChange={(e) => handleChange(e, 'name')}
                      />
                    </Grid>

                    <Grid item xs={12}>
                      <Input
                        type="email"
                        name="email"
                        label="E-mail"
                        value={dataOut.email}
                        onChange={(e) => handleChange(e, 'email')}
                      />
                    </Grid>

                    <Grid item xs={12}>
                      <Input
                        name="date"
                        label="Data de nascimento"
                        value={dataOut.date}
                        onChange={(e) => handleChange(e, 'date')}
                        InputProps={{
                          inputComponent: dateMask,
                        }}
                      />
                    </Grid>
                  </Grid>
                )}

                {loading ? (
                  <div className={classes.loading}>
                    <CircularProgress style={{ color: '#744EAA' }} size={40} />
                  </div>
                ) : (
                  <Button fullWidth className={classes.btn1} type="submit">
                    Registrar
                  </Button>
                )}
              </Form>
            </Grid>

            <Grid item xs={12}>
              <Typography className={classes.copyright}>
                Copyright ©Joti 2020
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
}
