import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { Grid, Typography, Hidden, Button } from '@material-ui/core';
import clsx from 'clsx';
import useStyles from './styles';
import organizationWhite from '../../../../assets/icons/organizationWhite.svg';
import Modal from './Modal';

// import { Container } from './styles';

export default function Task({ data, att }) {
  const myTeam = useSelector((state) => state.team.team);
  const [open, setOpen] = useState(false);

  const classes = useStyles();

  function level() {
    if (myTeam) {
      if (data.answer) {
        if (
          data.answer.status === 'Enviado para correção' ||
          data.answer.status === 'Enviada para recorrecao'
        ) {
          return 1;
        }

        if (
          data.answer.status === 'Corrigida' ||
          data.answer.status === 'Recorrigida'
        ) {
          return 2;
        }
      }

      return 0;
    } else {
      return 2;
    }
  }

  return (
    <Grid item xs={6} sm={4} md={3} lg={2}>
      <Button
        fullWidth
        style={{ height: '100%' }}
        onClick={() => setOpen(true)}
      >
        <Grid
          container
          spacing={1}
          alignContent="space-between"
          style={{ width: '100%', height: '100%' }}
        >
          <Grid
            item
            xs={12}
            justify="center"
            alignItems="center"
            style={{ display: 'flex' }}
          >
            <div
              className={clsx(
                classes.circle,
                level() === 0
                  ? classes.level0
                  : level() === 1
                  ? classes.level1
                  : classes.level2
              )}
            >
              <div
                className={classes.circleInside}
                style={
                  level() === 2
                    ? {
                        backgroundColor: '#483699',
                      }
                    : { backgroundColor: '#B7A7FE' }
                }
              >
                <img src={organizationWhite} className={classes.icon} />
              </div>
            </div>
          </Grid>
          <Grid item xs={12} justify="center" alignItems="center">
            <Typography className={classes.name} noWrap>
              {data.task.title}
            </Typography>
          </Grid>
          <Grid item xs={12} justify="center" alignItems="center">
            {myTeam && (
              <Typography className={classes.status}>
                {data.answer ? data.answer.status : 'Não respondida'}
              </Typography>
            )}
          </Grid>
        </Grid>
      </Button>
      <Modal task={data} open={open} close={() => setOpen(false)} att={att} />
    </Grid>
  );
}
