/* eslint-disable no-sparse-arrays */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useRef } from 'react';
import { useSelector } from 'react-redux';
import { Dialog, Typography, Grid, Button } from '@material-ui/core';
import { Form } from '@unform/web';
import * as Yup from 'yup';
import { toast } from 'react-toastify';

import api from '../../../../../services/api';

import Input from '../../../../../components/Input';

import useStyles from './styles';
import Loader from '../../../../../components/Loader';

export default function Modal({ task, open, close, att }) {
  const classes = useStyles();
  const formRef = useRef();
  const me = useSelector((state) => state.user.me);
  const myTeam = useSelector((state) => state.team.team);
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(false);
  const [loadingSend, setLoadingSend] = React.useState(false);

  async function submit(e) {
    const responses = Object.values(e);

    let t = [];

    responses.map((response, index) => {
      t.push({
        taskIdItem: data.taskItens[index]._id,
        answer: response,
      });
    });

    setLoadingSend(true);

    try {
      await api.post(`/answers/${myTeam.teamId}`, {
        taskId: data._id,
        taskItens: t,
        groupRegion: me.region,
      });

      setLoadingSend(true);
      toast.success('Tarefa respondida com sucesso.');
      att();
    } catch (error) {
      setLoadingSend(false);
      toast.error('Ocorreu algum erro, tente novamente mais tarde');
    }
  }

  async function getTask() {
    setLoading(true);
    try {
      const response = await api.get(`tasks/detail/${myTeam.teamId}`, {
        params: {
          type: myTeam.type,
          taskId: task.task._id,
        },
      });

      setData(response.data);

      console.log(response.data.instructions.length);

      setLoading(false);
    } catch (error) {
      setLoading(false);
      toast.error('Ocorreu algum erro, tente novamente mais tarde.');
    }
  }

  React.useEffect(() => {
    if (open) {
      getTask();
    }
  }, [, open]);

  return (
    <Dialog open={open} onClose={() => close()} maxWidth="md">
      <Grid container spacing={2} className={classes.paper}>
        <Grid item justify="flex-start" xs={12}>
          <Typography className={classes.title}>
            Desafio {data && data.number}
          </Typography>
        </Grid>
        <Grid item justify="flex-start" xs={12}>
          <Typography className={classes.subTitle}>
            {data && data.title}
          </Typography>
        </Grid>

        {loading ? (
          <div style={{ flex: 1, height: 200 }}>
            <Loader />
          </div>
        ) : (
          <>
            <Grid item xs={12}>
              <Grid
                container
                spacing={2}
                alignItems="center"
                justify="space-between"
              >
                {data &&
                  data.instructions.map((instruction) => (
                    <Grid
                      item
                      xs={12}
                      sm={data.instructions.length === 1 ? 12 : 6}
                    >
                      {instruction.link_image && (
                        <img
                          src={instruction.link_image}
                          style={{
                            maxHeight: 200,
                            maxWidth: '100%',
                            width:
                              data.instructions.length === 1 ? '100%' : 'auto',
                          }}
                        />
                      )}
                    </Grid>
                  ))}
              </Grid>
            </Grid>

            <Grid item xs={12} justify="flex-start">
              <Typography className={classes.label}>O que fazer?</Typography>
            </Grid>

            {data && (
              <Grid item xs={12} justify>
                <Form ref={formRef} onSubmit={submit} className={classes.form}>
                  <Grid container spacing={2}>
                    {data.taskItens.map((item, index) => (
                      <Grid item xs={12}>
                        <Typography className={classes.value}>
                          {item.usersText}
                        </Typography>

                        <Input
                          name={`${index}`}
                          hidden
                          label={item.campName || 'sem nome'}
                          style={{
                            visibility: item.camp ? 'visible' : 'hidden',
                            height: item.camp ? 'auto' : 0,
                            marginTop: item.camp ? 10 : 0,
                          }}
                        />
                      </Grid>
                    ))}

                    <Grid item xs={12}>
                      <Grid container spacing={0}>
                        <Grid item xs={12}>
                          <Typography className={classes.status}>
                            Status:
                            <Typography className={classes.answer}>
                              {' '}
                              {data.answered
                                ? ' respondida'
                                : ' Nao respondida'}
                            </Typography>
                          </Typography>
                        </Grid>
                        {data.answered && (
                          <Grid item xs={12}>
                            <Typography className={classes.status}>
                              Puntuação:
                              <Typography className={classes.answer}>
                                {' '}
                                80/100
                              </Typography>
                            </Typography>
                          </Grid>
                        )}
                        <Grid item xs={12}>
                          <Typography className={classes.status}>
                            Autor:
                            <Typography className={classes.answer}>
                              {' '}
                              {data.author}
                            </Typography>
                          </Typography>
                        </Grid>
                      </Grid>
                    </Grid>

                    <Grid item xs={12}>
                      <Button className={classes.button} type="submit">
                        Enviar
                      </Button>
                    </Grid>
                  </Grid>
                </Form>
              </Grid>
            )}
          </>
        )}
      </Grid>
    </Dialog>
  );
}
