import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  SwipeableDrawer,
  List,
  ListItem,
  Typography,
  ListItemAvatar,
  ListItemText,
  Avatar,
  ListItemIcon,
} from '@material-ui/core';
import history from '../../../../services/history';
import useStyles from './styles';
import { signOut } from '../../../../store/modules/auth/actions';

import out from '../../../../assets/icons/out.svg';
import organizationBlack from '../../../../assets/icons/organizationBlack.svg';
import organization from '../../../../assets/icons/organization.svg';
import teamBlack from '../../../../assets/icons/teamBlack.svg';
import team from '../../../../assets/icons/team.svg';
import taskBlack from '../../../../assets/icons/taskBlack.svg';
import task from '../../../../assets/icons/task.svg';

import Gravatar from 'react-gravatar';

import Modal from '../../../../components/Modal';
import Header from '../Header';

export default function MobileSidebar({ open, trogle }) {
  const me = useSelector((state) => state.user.me);
  const dispatch = useDispatch();
  const classes = useStyles();
  const [openModal, setOpenModal] = useState(false);

  function selected(url) {
    return window.location.href.indexOf(url) !== -1;
  }

  async function handleOut() {
    dispatch(signOut());
  }

  return (
    <SwipeableDrawer
      anchor="left"
      open={open}
      onClose={() => trogle()}
      className={classes.drawer}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <Header open={open} trogle={() => trogle()} />
      <List>
        <ListItem className={classes.profile}>
          <ListItemAvatar>
            <Gravatar
              email="souzakissi@gmail.com"
              className={classes.avatar}
              size={150}
            />
          </ListItemAvatar>
          <ListItemText>
            <Typography className={classes.name} noWrap>
              {me.name}
            </Typography>
            {me.team && me.team.level && (
              <Typography className={classes.level}>{me.team.level}</Typography>
            )}
          </ListItemText>
        </ListItem>
      </List>
      <List style={{ flex: 1 }}>
        <ListItem
          button
          onClick={() => {
            history.push('dashboard');
            trogle();
          }}
        >
          <ListItemIcon>
            <img src={selected('dashboard') ? team : teamBlack} />
          </ListItemIcon>
          <ListItemText>
            <Typography
              className={
                selected('dashboard') ? classes.selected : classes.normal
              }
            >
              Minha Equipe
            </Typography>
          </ListItemText>
        </ListItem>

        <ListItem
          button
          onClick={() => {
            history.push('task');
            trogle();
          }}
        >
          <ListItemIcon>
            <img src={selected('task') ? task : taskBlack} />
          </ListItemIcon>
          <ListItemText>
            <Typography
              className={selected('task') ? classes.selected : classes.normal}
            >
              Desafios
            </Typography>
          </ListItemText>
        </ListItem>

        <ListItem
          button
          onClick={() => {
            history.push('organization');
            trogle();
          }}
        >
          <ListItemIcon>
            <img
              src={selected('organization') ? organization : organizationBlack}
            />
          </ListItemIcon>
          <ListItemText>
            <Typography
              className={
                selected('organization') ? classes.selected : classes.normal
              }
            >
              Organização JOTI
            </Typography>
          </ListItemText>
        </ListItem>

        <ListItem button onClick={() => setOpenModal(true)}>
          <ListItemIcon>
            <img src={out} />
          </ListItemIcon>
          <ListItemText>
            <Typography className={classes.normal}>Sair</Typography>
          </ListItemText>
        </ListItem>
      </List>
      <ListItem>
        <Typography className={classes.copyright}>
          Copyright ©Joti 2020
        </Typography>
      </ListItem>

      <Modal
        open={openModal}
        close={() => setOpenModal(false)}
        handle={() => handleOut()}
        title="Encerrar sessão"
        text="Tem certeza que deseja sair da Plataforma JOTI?"
        bt1Text="Sair"
        bt2Text="Cancelar"
      />
    </SwipeableDrawer>
  );
}
