import React from 'react';
import { useDispatch } from 'react-redux';
import { getMeRequest } from '../../../../../store/modules/user/actions';
import {
  Dialog,
  Typography,
  Button,
  Grid,
  CircularProgress,
} from '@material-ui/core';
import useStyles from './styles';
import { Form } from '@unform/web';
import Input from '../../../../../components/Input';
import * as Yup from 'yup';
import validator from './validator';
import { toast } from 'react-toastify';
import api from '../../../../../services/api';

const CodeTeam = ({ open, close }) => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const formRef = React.useRef();
  const [teamCode, setTeamCode] = React.useState('');
  const [loading, setLoading] = React.useState(false);

  async function handle(data) {
    formRef.current.setErrors({});

    try {
      setLoading(true);

      await validator(data);

      await api.post('/team/join', {
        teamId: data.teamCode,
      });

      dispatch(getMeRequest());
      close();

      setLoading(false);
    } catch (err) {
      const validationErrors = {};
      if (err instanceof Yup.ValidationError) {
        err.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });

        formRef.current.setErrors(validationErrors);
      } else {
        toast.error('Ocorreu algum erro, tente novamente mais tarde.');
      }

      setLoading(false);
    }
  }

  return (
    <Dialog
      open={open}
      onClose={close}
      maxWidth="xs"
      style={{ overflowX: 'hidden' }}
    >
      <Grid container spacing={2} className={classes.paper}>
        <Grid item xs={12}>
          <Typography className={classes.title}>
            Entrar em equipe por codigo
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography className={classes.text}>
            Digite o codigo da equipe abaixo para fazer parte dela!
          </Typography>
        </Grid>

        <Grid item xs={12}>
          <Form ref={formRef} onSubmit={() => handle({ teamCode })}>
            <Input
              name="teamCode"
              label="Codigo"
              value={teamCode}
              onChange={(e) => setTeamCode(e.target.value)}
            />
          </Form>
        </Grid>

        <Grid item xs={12}>
          <Button
            onClick={() => handle({ teamCode })}
            fullWidth
            className={classes.button1}
          >
            {loading ? (
              <div className={classes.loading}>
                <CircularProgress className={classes.circular} size={30} />
              </div>
            ) : (
              'Entrar'
            )}
          </Button>
        </Grid>
      </Grid>
    </Dialog>
  );
};

export default CodeTeam;
